import React from "react";
import "./App.css";
import Tree from "./components/tree";
import { DEFAULT_NODES } from "./constants";

var fileReader;
var apiData;

class App extends React.Component {
  state = {
    newData: false,
    myData: DEFAULT_NODES,
    uploadedData: null,
    updatedKey: 1,
  };

  handleFileChosen = (file) => {
    fileReader = new FileReader();
    fileReader.readAsText(file);
    fileReader.onloadend = this.handleFileRead;
  };

  handleFileRead = () => {
    const content = fileReader.result;
    console.log(content);
    console.log(this.state);

    this.setState(
      {
        newData: true,
        uploadedData: JSON.parse(content),
      },
      () => {
        console.log(this.state.newData);
        console.log(this.state);
      }
    );
  };

  handleForceUpdate = () => {
    this.setState({ updatedKey: 2 });
  };

  handleGetAPI = () => {
    fetch("https://lancmlp.github.io/MLP-JSON-API/Data/MLPTreeData.json")
      .then((response) => response.json())
      .then((data) => {
        console.log(data);
        apiData = data;
        console.log(apiData);
        const content = apiData;
        console.log(content);
        console.log(this.state);
        this.setState(
          {
            newData: true,
            uploadedData: content,
          },
          () => {
            console.log(this.state.newData);
            console.log(this.state);
          }
        );
        this.setState({ updatedKey: 2 });
      });
  };

  render() {
    return (
      <React.Fragment>
        <div>
          <input
            type="file"
            onChange={(e) => this.handleFileChosen(e.target.files[0])}
          />
          <button onClick={this.handleForceUpdate}>Click Here to Upload</button>
          <button style={{ marginLeft: "50px" }} onClick={this.handleGetAPI}>
            Click Here to Get Current Tree
          </button>
        </div>
        <Tree
          key={this.state.updatedKey}
          data={
            this.state.newData === true
              ? this.state.uploadedData
              : this.state.myData
          }
        />
      </React.Fragment>
    );
  }
}

export default App;
