import React from "react";
import Collapsible from "react-collapsible";
import EditableItem from "../editable-item";
import "./tree-node.css";

const TreeNode = ({ children, ...otherProps }) => {
  const hasChildren = children !== undefined;

  const renderChildren = (children) => {
    return (
      <ul>
        {children.map((nodeProps) => {
          const { id, ...others } = nodeProps;
          return <TreeNode key={id} {...others} />;
        })}
      </ul>
    );
  };

  const buttonName = () => {
    if (otherProps.title === "") {
      return "New Item";
    } else {
      return otherProps.title;
    }
  };

  return (
    <li>
      <Collapsible
        trigger={
          <button style={{ margin: "3px", padding: "3px", maxWidth: "300px" }}>
            {buttonName()}
          </button>
        }
        transitionTime={200}
      >
        <div className="TreeNode">
          <EditableItem {...otherProps} />
        </div>
        {hasChildren && renderChildren(children)}
      </Collapsible>
    </li>
  );
};

export default TreeNode;
